import { lazy } from 'react';

// project imports
import MainLayout from 'layout/MainLayout';
import Loadable from 'ui-component/Loadable';

// dashboard routing
// utilities routing
const Home = Loadable(lazy(() => import('views/home')));
const Product = Loadable(lazy(() => import('views/product')));
const ProductDraft = Loadable(lazy(() => import('views/product/ProductDraft')));
const ProductEdit = Loadable(lazy(() => import('views/product/ProductEdit')));
const Photo = Loadable(lazy(() => import('views/photo')));
const Service = Loadable(lazy(() => import('views/service')));
const Order = Loadable(lazy(() => import('views/order')));
const Upgrade = Loadable(lazy(() => import('views/upgrade')));

// ==============================|| MAIN ROUTING ||============================== //

const MainRoutes = {
    path: '/',
    element: <MainLayout />,
    children: [
        {
            path: '/public/index.html',
            element: <Home />
        }, //for mounting homepage in a specific path
        {
            path: '/',
            element: <Home />
        },
        {
            path: '/product',
            element: <Product />
        },
        {
            path: `product/draft/:groupId`,
            element: <ProductDraft />
        },
        {
            path: `product/:productId`,
            element: <ProductEdit />
        },
        {
            path: '/photo',
            element: <Photo />
        },
        {
            path: '/order',
            element: <Order />
        },
        {
            path: '/upgrade',
            element: <Upgrade />
        },
        {
            path: '/service',
            element: <Service />
        }
    ]
};

export default MainRoutes;
