import { useState } from 'react';
import { useSelector } from 'react-redux';

import { createTheme, ThemeProvider } from '@mui/material/styles';
import { CssBaseline, StyledEngineProvider } from '@mui/material';

// routing
import Routes from 'routes';

// defaultTheme
import themes from 'themes';

// project imports
import NavigationScroll from 'layout/NavigationScroll';
import { zhCN, enUS } from '@mui/material/locale';
import Notice from 'ui-component/Notice';

// ==============================|| APP ||============================== //

const App = () => {
    const customization = useSelector((state) => state.customization);
    const localization = useSelector((state) => state.localization);
    const themeWithLocale = createTheme(themes(customization), localization.locale === 'zhCN' ? zhCN : enUS);
    //queryResult 相关
    let [queryResultType, setQueryResultType] = useState(false);
    let [queryResultInfo, setQueryResultInfo] = useState('');

    function queryResult(type, info) {
        setQueryResultType(type);
        if (typeof info === 'object') {
            let finalInfo = '';
            for (let [k, v] of Object.entries(info)) {
                finalInfo += JSON.stringify(k) + ': ' + JSON.stringify(v) + '\n';
            }
            info = finalInfo;
        } else if (typeof info !== 'string') info = String(info);
        setQueryResultInfo(info);
    }
    window.queryResult = queryResult;

    return (
        <StyledEngineProvider injectFirst>
            <ThemeProvider theme={themeWithLocale}>
                <CssBaseline />
                <NavigationScroll>
                    <Routes />
                </NavigationScroll>
                <Notice type={queryResultType} info={queryResultInfo} onClose={() => setQueryResultInfo(null)} />
            </ThemeProvider>
        </StyledEngineProvider>
    );
};

export default App;
