import PropTypes from 'prop-types';
import { Alert, Snackbar, Box } from '@mui/material';

//用于显示请求结果
const Notice = ({ type, info, onClose }) => {
    //pure functions
    if (!info) return null;
    let infoArr = info.split('\n');
    return (
        <Snackbar
            anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'right'
            }}
            open
            onClose={onClose}
            autoHideDuration={type ? 3000 : 9000}
        >
            <Alert variant="filled" severity={type ? 'success' : 'error'} title="check Console for more details">
                {infoArr.map((info, index) => (
                    <Box
                        key={index}
                        sx={{
                            textOverflow: 'ellipsis',
                            overflow: 'hidden',
                            maxWidth: '700px',
                            whiteSpace: 'normal'
                        }}
                    >
                        {info}
                    </Box>
                ))}
            </Alert>
        </Snackbar>
    );
};

Notice.propTypes = {
    type: PropTypes.bool,
    info: PropTypes.string,
    onClose: PropTypes.func
};
export default Notice;
